'use client'
import { formatDate } from "@/lib/basicUtils";
import { usePathname } from "next/navigation";
import React, { useEffect, useState } from "react";

interface ShortLink {
  code: string;
  url: string;
  last_clicked: string;
  clicks: number;
  created_at: string;
  updated_at: string;
}

const LinkViewPage: React.FC = () => {

  const path = usePathname();
  const segments = path?.split("/").filter(Boolean); // remove empty strings
  const lastParam = segments?.[segments.length - 1]; // "abcd123"

  console.log("Last path param:", lastParam);
  const [links, setLinks] = useState<ShortLink[]>([]);
  const [loading, setLoading] = useState<boolean>(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    const fetchLinks = async () => {
      try {
        const res = await fetch(`http://localhost:3001/links/${lastParam}`);
        const json = await res.json();
        setLinks(Array.isArray(json.data) ? json.data : []);
      } catch (err: any) {
        setError(err.message || "Failed to fetch links");
      } finally {
        setLoading(false);
      }
    };

    fetchLinks();
  }, []);

  if (loading) return <p>Loading links...</p>;
  if (error) return <p style={{ color: "red" }}>Error: {error}</p>;

  if (links.length === 0) return <p>No links found.</p>;

  return (
    <div className="flex justify-center p-6">
      <div className="max-w-2xl w-full bg-white shadow-xl rounded-2xl p-6 space-y-6">
        {/* -------- VIEW SECTION -------- */}
        <div className="border-b pb-4">
          <h2 className="text-2xl font-bold mb-2 text-ce text-gray-800 text-center">
            Link Details
          </h2>

          {/* First  */}
          <div className="text-gray-700 mb-1">
            <span className="font-semibold text-xl">Short Code:</span>{" "}
            <a
              href={`http://localhost:3001/${links[0].code}`}
              target="_blank"
              rel="noopener noreferrer"
              className="text-blue-600 hover:underline text-xl"
            >
              {links[0].code}
            </a>


          </div>
          <p className="text-gray-700 mb-1">
            <span className="font-semibold">Original URL:</span>{" "}
            <a
              href={links[0].url}
              target="_blank"
              rel="noopener noreferrer"
              className="text-blue-500 hover:underline"
            >
              {links[0].url}
            </a>
          </p>
          <div className="flex flex-col gap-2 text-gray-600 mt-2">
            <span><span className="font-semibold">Clicks:</span> {links[0].clicks}</span>
            <span><span className="font-semibold">Last Clicked:</span> {links[0].last_clicked ?? "Never"}</span>
            <span><span className="font-semibold">Created At:</span> {formatDate(links[0].created_at)}</span>
            <span><span className="font-semibold">Updated At:</span> {formatDate(links[0].updated_at)}</span>
          </div>
        </div>

        {/* -------- MANAGE SECTION -------- */}
        <div className="space-y-4">
          <h3 className="text-xl font-semibold text-gray-800">Manage Link</h3>

          <div className="flex flex-col md:flex-row md:items-center md:gap-4 space-y-2 md:space-y-0">
            <input
              type="text"
              placeholder="Edit URL"
              className="w-full md:flex-1 border rounded-lg p-2 focus:ring-2 focus:ring-blue-400"
            //  value={editedUrl}
            //   onChange={(e) => setEditedUrl(e.target.value)}
            />
            <button
              className="bg-blue-600 hover:bg-blue-700 text-white px-4 py-2 rounded-lg transition"
            //   onClick={handleUpdateUrl}
            >
              Update URL
            </button>
            <button
              className="bg-red-600 hover:bg-red-700 text-white px-4 py-2 rounded-lg transition"
            //   onClick={handleDeleteLink}
            >
              Delete Link
            </button>
          </div>

          <div className="flex flex-col md:flex-row md:items-center md:gap-4 mt-2">
            <button
              className="bg-gray-200 hover:bg-gray-300 text-gray-800 px-4 py-2 rounded-lg transition"
            // onClick={handleCopyShortUrl}
            >
              Copy Short URL
            </button>
            <button
              className="bg-gray-200 hover:bg-gray-300 text-gray-800 px-4 py-2 rounded-lg transition"
            //  onClick={handleCopyOriginalUrl}
            >
              Copy Original URL
            </button>
          </div>
        </div>
      </div>
    </div>


  );
};

export default LinkViewPage;
